class Projects extends Component {
  constructor(props) {
    super(props);
    this.state = {
      projectList: projectList,
    }
  }
  render() {
    return (
      <div className="projects">
            <Container>
              {this.state.projectList.map(function(project) {
                var mediadisplay;
                if (project.iframe) {
                  mediadisplay = <iframe title={project.iframe.title} src={project.iframe.src} frameBorder="0" allowFullScreen> </iframe>;
                } else {
                  mediadisplay = <img className={project.img.class} src={project.img.src} alt={project.img.alt}/>;
                }
                return (
                  <Fade>
                  <Row className="project">
                    <Col sm={6}>
                      <div className="media">
                        {mediadisplay}
                      </div>
                    </Col>
                    <Col sm={6}>
                      <div className="description">
                        <div>
                      <header>{project.name}</header>
                      <p>{project.text}</p>
                      <small>{project.smalltext ? project.smalltext : null}</small>
                      <div className="icons">
                      {project.icons ? project.icons.map(function(icon) {
                        var icondisplay;
                        if (icon.fontawesome) {
                          icondisplay = <FontAwesomeIcon icon={icon.fontawesome} />;
                        } else {
                          icondisplay = icon.name;
                        }
                        return (                          
                          <a className="icon" target="_blank" rel="noopener noreferrer" href={icon.href}> {icondisplay} </a> 
                        );
                      }) : null}
                      </div>
                    </div>
                  </div>
                </Col>
              </Row>
              </Fade>
              );
            })}
            </Container>
          </div>
    );
  }
}

export default Projects;
